@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	font-family: "Sora", sans-serif;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	/* color: #fff; */
	/* background-color: #011; */
}
.photo {
	height: 60px;
	width: 60px;
}

/* theme
light: #738cff
primary: #305fe2
dark: #0036af */

.blog-card-container {
	box-shadow: 0px 0px 15px -7px rgba(0, 0, 0, 0.94);
	background-color: #738cff;
	color: #fff;
	padding: 10px;
	max-width: 700px;
	overflow: hidden;
	border-radius: 20px;
	margin: 20px;
	padding: 10px;
}
.blog-image-container {
	max-width: 200px;
	overflow: hidden;
	border-radius: 20px;
	margin: 20px;
}
.blog-description-container {
	max-width: 400px;
	overflow: hidden;
	margin: 20px;
}

.name {
	font-size: 40px;
	text-align: center;
}

.title {
	font-size: medium;
	background-color: #738cff;
	margin: 10px;
	padding: 5px;
	border-radius: 5px;
	color: #fff;
}

.card-container {
	max-width: 700px;
	overflow: hidden;
	box-shadow: 0px 0px 15px -7px rgba(0, 0, 0, 0.94);
	border-radius: 20px;
	margin: 20px;
	border-color: #000;
	padding: 10px;
}
.image-container {
	max-width: 200px;
	overflow: hidden;
	border-radius: 20px;
	margin: 20px;
}
.description-container {
	max-width: 400px;
	overflow: hidden;
	margin: 20px;
}

